#tip{
    width:980px;
    margin:0 auto;
    border-top:5px solid red;
}
.lef{
    width:400px;
    padding:50px 20px;
    border-right:1px solid #ccc;
    float: left;
}
.lef h2{
    color:#222;
}
.lef dl{
    text-align: center;
}
.lef dl img{
    height:100px;
}
.lef dd{
    height:44px;
    overflow: hidden;
}
.rig{
    float:right;
    width:578px;
    padding:50px 20px;
    border-right:1px solid #ccc;
    
}
.rig h2{
    color:#222;
}
.rig ul{
    width:536px;
    height:40px;
    background: #f1f1f1;
    border-top:1px solid #999;
}
.rig li{
    float: left;
    line-height: 40px;
    width:108px;
}
.lis span{
    display: block;
    float: left;
    width:108px;
    text-align: center;
    height:40px;
    overflow: hidden;
}
.lis .anticon{
    width:40px;
    height:40px;
    padding-left:20px;
    cursor: pointer;
}
.lis{
    height:40px;
    background: white;
    line-height: 40px;
    
}
.lis:nth-of-type(odd){
    height:40px;
    background:#e8e8e8;
    line-height: 40px;
}
.pic{
    width:160px;
    height:160px;
    float: left;
    margin:8px 8px;
}





.lis a span:hover{
    text-decoration: underline;
}