.list-wrap{
    width: 100%;
    min-height: 1000px;
    background: #f5f5f5;
}
.list-w{
    width: 980px;
    min-height: 700px;
    margin: 0 auto;
    background-color: #fff;
    border: 1px solid #d3d3d3;
    border-width: 0 1px;
    overflow: hidden;
}
.list-wl{
    width: 708px;
    min-height: 700px;
    border-right: 1px solid #d3d3d3;
    float: left;
    padding: 47px 30px 40px 39px;
}

.list-1>img{
    width: 208px;height: 208px;display: block;float: left;
}
.list-1{
    min-height: 250px;
}


.list-1r{
    float: left;margin-left: 25px
}
.list-1r>p{
    width: 250px;
    line-height: 24px;
    font-size: 20px;
    font-weight: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
}
.list-1r>div:nth-of-type(1){
    overflow: hidden;
}
.list-1r>div:nth-of-type(1) img{
    width: 35px;height: 35px;display:block;float: left;border-radius:5px; 
}
.list-1r>div:nth-of-type(1) span:nth-of-type(1){
    color: #0c73c2;
    float: left;font-size: 12px;
    line-height: 35px;
    float: left;
    margin-left: 15px;
}

.list-1r>div:nth-of-type(1) span:nth-of-type(2){
    color: #999;
    font-style: normal;
    float: left;font-size: 12px;
    line-height: 35px;
    float: left;margin-left: 15px;
}
.list-1r>div:nth-of-type(2){
        overflow: hidden;
        margin-top: 20px;
}
.list-1r>div:nth-of-type(2)>button{
    width: 50px;
    height: 30px;margin-right: 20px;background: #f2f2f2;
    border: 1px solid #c4c4c4;
    color: #000;

}
.list-1r>div:nth-of-type(2)>button:nth-of-type(1){
    width: 64px;
    height: 30px;
    float: left;
    background: #4291da;
    color: #fff;
    text-align: center;
    line-height: 30px;
    border-radius: 3px;
}
.list-1r>div:nth-of-type(2)>button:nth-of-type(1)>span:nth-child(1){
    margin-left: -8px;
}
.list-1r>div:nth-of-type(2)>button:nth-of-type(1)>span:nth-child(2){
    margin-left: 5px;
}
.list-1r>div:nth-of-type(3){
    margin-top: 20px;
}
.list-1r>div:nth-of-type(3)>span{
    padding-right: 16px;
    height: 22px;
    margin-top: 25px;
    color: #777;font-size: 12px;
}
.list-1r>div:nth-of-type(3)>button{
    display: inline-block;
    height: 18px;
    width: 62px;
    line-height: 18px;
    text-align: center;
    border: 1px solid #c00;
    margin-right: 10px;
    color: #c00;    border-radius: 5px;
    font-size: 12px;
}
.list-2{
    height: 50px;
    border-bottom: 2px solid #c20c0c;
}
.list-2 p{
    font-family: "Microsoft Yahei", Arial, Helvetica, sans-serif;font-size: 24px;
    font-weight: normal;
    display: inline-block;
}


.list-3{
    height: 35px;
    border: 1px solid #ccc;background: #fff;line-height: 35px;
}
.list-3>span:nth-of-type(1){
    width: 77px;height: 33px;border-right:1px solid #ccc; display: block;float: left;
    padding-left: 20px;    font-size: 12px;

}
.list-3>span:nth-of-type(2){
    width: 378px;height: 33px;border-right:1px solid #ccc;  display: block;float: left;
    padding-left: 20px;    font-size: 12px;
}
.list-3>span:nth-of-type(3){
    width: 90px;height: 33px;border-right:1px solid #ccc;  display: block;float: left;
    padding-left: 20px;    font-size: 12px;
}
.list-3>span:nth-of-type(4){
    width: 90px;height: 33px; display: block;float: left;
    padding-left: 20px;    font-size: 12px;
}



.list-4{
    min-height: 50px;
    border: 1px solid #ccc;background: #fff;line-height: 50px;overflow: hidden;
}

.list-4 li{
    overflow: hidden;
}
.list-4 li:nth-of-type(odd){
    background: #f7f7f7;
}



.list-4 li>span:nth-of-type(1){
    width: 77px;height: 50px;border-right:1px solid #ccc; display: block;float: left;
    padding-left: 20px;    font-size: 12px;

}
.list-4 li>span:nth-of-type(2){
    width: 378px;height: 50px;border-right:1px solid #ccc;  display: block;float: left;
    padding-left: 20px;    font-size: 12px;
}
.list-4 li>span:nth-of-type(2)>img{
    width: 30px;height: 30px; display: inline-block;
}
.list-4 li>span:nth-of-type(2)>span:nth-of-type(1){
    margin-left: 10px;
}
.list-4 li>span:nth-of-type(2)>a{
    margin-left: 10px;
}
.list-4 li>span:nth-of-type(2)>a:hover{
    text-decoration: underline;
}
.list-4 li>span:nth-of-type(3){
    width: 90px;height: 50px;border-right:1px solid #ccc;  display: block;float: left;
    padding-left: 20px;    font-size: 12px;
}
.list-4 li>span:nth-of-type(4){
    width: 90px;height: 50px; display: block;float: left;
    padding-left: 20px;    font-size: 12px;
}




















.list-wr{
    width: 269px;
    min-height: 700px;

    float: right;
    padding: 20px 40px 40px 30px;
}
.list-wr1{
    position: relative;
    height: 23px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    color: #333;    font-weight: bold;    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
}
.list-wr2{
    display: flex;justify-content: space-between;
    padding-bottom: 25px;
}
.list-wr2 li{
    float: left;
width: 40px;
height: 40px;
}
.list-wr2 li img{
    width: 40px;
    height: 40px;
    display: block;
}    
.list-wr3{
    position: relative;
    height: 23px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    color: #333;font-size: 12px;
    font-weight: bold;    font-family: Arial, Helvetica, sans-serif;
}
.list-wr4 li{
    width: 200px;
    height: 50px;
    margin-bottom: 15px;
    line-height: 24px;
}
.list-wr4 li>div:nth-child(1){
    float: left;
    width: 50px;
    height: 50px;
}
.list-wr4 li>div:nth-child(1) img{
    display: block;width: 50px;
    height: 50px;
}
.list-wr4 li>div:nth-child(2){
    margin-left: 60px;
    line-height: 24px;
}
.list-wr4 li>div:nth-child(2)>p:nth-child(1){
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
}
.list-wr4 li>div:nth-child(2)>p:nth-child(2){
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
    font-size: 12px;    color: #666;margin-top: -10px;
}













.gd-footer{
    width: 100%;
    height: 150px;
    padding-top: 20px;
    border-top:1px solid #ccc; 
    background: #f2f2f2;
}
.gd-footer img{
    display: block;
    width: 980px;height:100px ;
    margin: 0 auto;
}


#gd-top{
    width: 50px;height: 50px;
    position: fixed;
    right: 100px;bottom: 100px;
    border: 1px solid #ccc;
    cursor: pointer;
    border-radius:5px; 
    display: none;

}
#gd-top span{
    display: block;
    width: 50px;height: 25px;
    text-align: center;line-height: 25px
}
#gd-top p{
    display: block;
    width: 50px;height: 25px;
    text-align: center;line-height: 25px
}