.gd-wrap{
    width: 100%;min-height: 1000px;background: #f5f5f5;overflow: hidden;
}
.gd-w{
    width: 980px;
    min-height: 700px;
    margin: 0 auto;
    background-color: #fff;
    border: 1px solid #d3d3d3;
    border-width: 0 1px;
    padding: 40px;
    overflow: hidden;
}
.gd1{
    height: 50px;
    border-bottom: 2px solid #c20c0c;
}
.gd1 p{
    font-family: "Microsoft Yahei", Arial, Helvetica, sans-serif;font-size: 24px;
    font-weight: normal;
    display: inline-block;
}

.gd2{
    overflow: hidden;
} 
.gd2 li{
    width: 140px;
    height: 200px;
    position: relative;
    float: left;margin-right: 49.5px;
    margin-top: 20px;
}
.gd2 li:nth-child(5n){
    margin-right: 0px;
}
.gd2 li img{
    width: 140px;height: 140px;display: block;
}
.gd2 li>div{
    width: 140px;
    height: 27px;
    line-height: 27px;
    color: #ccc;
    position: absolute;top: 113px;
    background: rgba(0,0,0,0.3)
}
.gd2 li>div span:nth-child(1){
    margin-left: 10px;
}
.gd2 li>div span:nth-child(2){
    margin-left: 10px;
}
.gd2 li>div span:nth-child(3){
    margin-left: 50px;
}


.gd2 li p:nth-of-type(1){
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
    color: #000;  font-size: 14px;
    margin-top: 8px;
}
.gd2 li p:nth-of-type(2){
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
    color: #666;  font-size: 12px;
    margin-top: -10px;
}

.gd-footer{
    width: 100%;
    height: 150px;
    padding-top: 20px;
    border-top:1px solid #ccc; 
    background: #f2f2f2;
}
.gd-footer img{
    display: block;
    width: 980px;height:100px ;
    margin: 0 auto;
}


#gd-top{
    width: 50px;height: 50px;
    position: fixed;
    right: 100px;bottom: 100px;
    border: 1px solid #ccc;
    cursor: pointer;
    border-radius:5px; 
    display: none;

}
#gd-top span{
    display: block;
    width: 50px;height: 25px;
    text-align: center;line-height: 25px
}
#gd-top p{
    display: block;
    width: 50px;height: 25px;
    text-align: center;line-height: 25px
}