    float: left;
    width: 75%;
}
.j_flag{   
    height: 330px;
    padding:0 40px;
    border-bottom: 3px solid #c20c0c;
}
.j_flag img{
    width: 200px;
    height: 200px;
    float: left;
    border: 1px solid #ccc;
    padding: 3px;
    margin-right: 35px;
}
.jj_flag{
    width: 100%;
    padding: 40px 0;
    overflow: hidden;
}
.jj_flag div{
    float: left;
    width: 64%;
    overflow: hidden;
}
.jj_flag div p{
    line-height: 30px;
    margin-left: 10px;
}
.jj_flag div pre{
    height: 60px;
    line-height: 30px;
    white-space:pre-wrap; 
    /* overflow: hidden; */
}
.jj_flag div h1{
    font-size:20px; 
    margin: 20px 0;
}
.jj_flag div button{
    margin: 0 10px;
}
.j_flag h2{
    line-height: 30px;
}
.j_flag h2 span{
    font-size: 14px;
}
.anticon-play-circle{
    cursor: pointer;
}
.ant-menu-item{
    height: 80px!important;
    border-bottom: 1px solid #d3d3d3;
    margin: 0px!important;
    padding:10px 0 10px 10px!important;
}
.ant-menu-item img{
    float: left;
    width: 60px;
    height: 60px;
}
.ant-menu-item h2{
    width: 150px;
    overflow: hidden;
    float: left;
    font-size: 14px;
    margin-left: 20px;
    margin-bottom: 0px!important;
    padding: 8px 0;
    line-height: 22px;
}
.ant-menu-item p{  
    margin-bottom: 0px;
}
.ant-menu-item span{
    font-size: 13px;
    color: #696969;
}