.container {
    overflow: hidden;
    margin-top: 30px;
    max-width: 47rem;
    margin-left: auto;
    margin-right: auto;
}

.container h1 {
    font-size: 54px;
    color: #333;
    margin: 30px 0 10px;
}

.container h2 {
    font-size: 22px;
    color: #555;
}

.container h3 {
    font-size: 24px;
    color: #555;
}

.container hr {
    display: block;
    width: 7rem;
    height: 1px;
    margin: 2.5rem 0;
    background-color: #eee;
    border: 0;
}
.info{
    overflow: hidden;
    float: left;
    border-radius: 50%;
    padding: 30px;
    background: rgb(58, 53, 53);
}
.info img{
    width: 200px;
    height: 200px;
    border-radius: 50%;
}
.container a {
    color: #08c;
    text-decoration: none;
}

.container p {
    font-size: 18px;
    font-size: 24px;
    font-weight: 600;
}
#player3{
    margin-top: 312px;
    margin-left: 250px;
    width: 350px;
    overflow: hidden;
    padding-bottom: 100px;
}
#player3>p{
font-size: 20px;
color: #333;
font-family: Arial, Helvetica, sans-serif;
}
#player3>span{
    margin-left: 32px;
    }

.aplayerG-lrc-content{
    width: 500px;
    font-size: 12px;
    color: #333;
    font-family: Arial, Helvetica, sans-serif;
    margin-left: 32px;
}
.aplayerG-lrc-content p{
    line-height: 50px;
}
.zhankai{
    color: #00f;
    cursor: pointer;
}
















.list-1r{
    float: left;margin-left: 80px;
    margin-top: 50px;
}
.list-1r>p{
    line-height: 24px;
    font-size: 20px;
    font-weight: normal;
}
.list-1r>div:nth-of-type(1){
    overflow: hidden;
}
.list-1r>div:nth-of-type(1) img{
    width: 35px;height: 35px;display:block;float: left;border-radius:5px; 
}
.list-1r>div:nth-of-type(1) span:nth-of-type(1){
    color: #0c73c2;
    float: left;font-size: 12px;
    line-height: 35px;
    float: left;
    margin-left: 15px;
}

.list-1r>div:nth-of-type(1) span:nth-of-type(2){
    color: #999;
    font-style: normal;
    float: left;font-size: 12px;
    line-height: 35px;
    float: left;margin-left: 15px;
}
.list-1r>div:nth-of-type(2){
        overflow: hidden;
        margin-top: 20px;
}
.list-1r>div:nth-of-type(2)>button{
    width: 50px;
    height: 30px;margin-right: 20px;background: #f2f2f2;
    border: 1px solid #c4c4c4;
    color: #000;

}
.list-1r>div:nth-of-type(2)>button:nth-of-type(1){
    width: 64px;
    height: 30px;
    float: left;
    background: #4291da;
    color: #fff;
    text-align: center;
    line-height: 30px;
    border-radius: 3px;
}
.list-1r>div:nth-of-type(2)>button:nth-of-type(1)>span:nth-child(1){
    margin-left: -8px;
}
.list-1r>div:nth-of-type(2)>button:nth-of-type(1)>span:nth-child(2){
    margin-left: 5px;
}
.list-1r>div:nth-of-type(3){
    margin-top: 20px;
}
.list-1r>div:nth-of-type(3)>span{
    padding-right: 16px;
    height: 22px;
    margin-top: 25px;
    color: #777;font-size: 12px;
}
.list-1r>div:nth-of-type(3)>button{
    display: inline-block;
    height: 18px;
    width: 62px;
    line-height: 18px;
    text-align: center;
    border: 1px solid #c00;
    margin-right: 10px;
    color: #c00;    border-radius: 5px;
    font-size: 12px;
}