}
.header{
  width: 100%;
  height: 70px;
  background: #242424;
  border-bottom: 1px solid #000;
}
.headCen{
  width: 1100px;
  height: 70px;
  margin: 0 auto;
  color: #fff;
}
.headCen h1{
  float: left;
  width: 176px;
  height: 69px;
  overflow: hidden;
}
.headCen h4{
  color:white;
  width:40px;
  float: left;
  margin-left:20px;
  line-height: 70px;
  margin-bottom: 0px;
}
.nav li{
  float: left;
  height: 70px;
  font-size: 14px;
  padding: 0 19px;
  text-align: center;
  line-height: 70px;
  
}
.nav li a{
  color: #ccc;
}
.nav li .active{
  color:#fff;
}

.search{
  float: left;
}
.search input{
  margin-top: 19px;
  border: 0;
  background: #fff;
  border-radius: 32px;
  line-height: 16px;
  width: 158px;
  height: 32px;
  float: left;
  padding-left: 26px;
  font-size: 12px;
  color: #000;
}
.my{
  float: left;
  margin: 19px 20px 0 20px;
  position: relative;
}
.my img{
  width:30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
}
.arr{
  width: 60px;
  height: 8px;
  position: absolute;
  left: 45px;
  top: -6px;
}
.arr i{
  display: inline-block;
  width: 14px;
  height: 8px;
  position: absolute;
  left: 20px;
  top: -0px;
}
.my ul{
    position: absolute;
    top: 35px;
    right: -70px;
    width: 158px;
    background: #2b2b2b;
    border: 1px solid #202020;
    box-shadow: 0 8px 24px 0 rgba(0,0,0,0.50);
    border-radius: 4px;
    font-size: 12px;
    z-index: 300;
}
.my li{
  line-height: 34px;
  padding-left: 24px;
  color: #ccc;
  cursor: pointer;
}
.my li em{
  font-style: normal;
}
.my li i svg{
  float: left;
  margin: 7px 15px 0 0;
}

