@import '~antd/dist/antd.css';

.log2{
    width:500px;
    height:340px;
    position:fixed;
    left:40%;
    top:20%;
    border:1px solid #444;
    
}
.log2 h2{
    height:40px;
    background: #444;
    line-height: 40px;
    color:white;
    margin:0;
}

.log2 button{
    margin-top:14px;
}
.li2{
    padding:26px 140px;
    background: white;
}
.li2 input{
    margin-top:6px;
    width:216px;
    border:1px solid #999;
}
.li2 input:nth-child(4){
    width:10px;
    height:10px;
}
.log2 i{
    position: absolute;
    right:20px;
    top:10px;
    color:white;
}
.hi{
    border-top:1px solid #999;
    margin-top:12px;
    height:66px;
    background: #eee;
    display: flex;
    justify-content:space-between;
    line-height: 66px;
    padding: 0 20px;
}