
#mine{
    width:980px;
    height:500px;
    border:1px solid #ccc;
    padding:30px;
    background:white;
    margin:0 auto;
    border-top:5px solid red;
}
#mine h1{
    height:60px;
}
#mine h4{
    height:40px;
    background:#f1f1f1;
    line-height: 40px;
    padding-left:20px;
}
#mine img{
    margin-top:20px;
    width:150px;
    height:150px;
    border: 2px solid red;

}
#mine div:nth-of-type(1){
    margin-top:20px;
    float: left;
    margin-left:20px;
    width:200px;
    height:200px;
    
}
#auser{
    margin-top:50px;
}
#auser i{
    color: #000;
    display: block;
    height:30px;
    
}
#mine input{
    
    width:200px;
    border:1px solid #000;
    margin:10px 0 20px 0;
    
}