.player{
    width: 100%;
    height: 100px;
    background: rgb(59, 56, 56);
    border-radius: 10px;
    position: fixed;
    bottom: -90px;
    padding: 0 20px;
    z-index: 200;
}
.alb{
    float: left;
    width: 220px;
    height: 100px;
}
.player .albumPic{
    background-size: cover; 
    width: 80px;
    height: 80px;
    float: left;
    border-radius: 10px;
    margin-top: 10px;
}
.player .trackInfo{
    float: left;
    padding: 10px 0 15px;
    font-size: 14px;
    white-space: nowrap;
    color: #fff;
    margin-left: 10px;
}
.trackInfo .name{
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: bold;
}
.proPar{
    float: left;
    height: 10px;
    width: 80%;
    border: 1px solid #ccc;
    border-radius: 10px;
    margin-top: 70px;
}
.player .progress{
    width: 100%; 
    height: 10px;    
    background: #00f;
    opacity: .4;
}
.controls{
    position: absolute;
    top: 10px;
    width: 100%;
    text-align: center;
    margin-left: 35px;
}
.play{
    cursor: pointer;
    width: 50px;
    height: 50px;
    border: 2px solid #ccc;
    border-radius: 50%;
    margin: auto;
    display: flex;
    align-items:center;
    justify-content:center;
    color: #fff;
    font-size: 35px;
}
.play:hover{
    font-size: 40px; 
}
.btn{
    cursor: pointer;
    position: absolute;         
    top: 25px;
    font-size: 30px;
}
.btn:hover{
    font-size: 32px;
}
.previous{
    position: absolute; 
    top:10px;       
    left: 480px;
    cursor: pointer;
}
.next{
    position: absolute; 
    top:10px;       
    right: 480px;
    cursor: pointer;
}
.time{
    padding-right: 20px;
    width:80%; 
    display: flex;
    position: absolute;
    bottom: 0px;
    align-items: center;
    justify-content: space-between; 
    margin-left: 215px;
    color: #fff;
    font-size: 12px;
}

.anticon-pause,.anticon-caret-right{
    color:#fff;
}

.anticon-fast-backward,.anticon-fast-forward{
    font-size: 30px;
    color: #fff;
}
