@import '~antd/dist/antd.css';

li{
    list-style: none;
  }
.right .anticon{
    border:1px solid #999;
    border-radius: 50%;
    font-size:26px;
    padding:6px;
    color:orangered;
    margin:9px;
}
.log1{
    width:500px;
    height:340px;
    position: fixed;;
    left:40%;
    top:20%;
    border:1px solid #444;
    background: white;
}
.log1 h2{
    height:40px;
    background: #444;
    line-height: 40px;
    /* margin-left:20px; */
    color:white;
}
.left{
    height:260px;
    width:280px;
    padding:30px;
    float: left;
    border-right:1px solid #999;
}
.left button{
    margin-top:14px;
}
.right{
    width:200px;
    float:right;
    padding:20px;
}
.right li{
    cursor: pointer;
}
.log1 em{
    position: absolute;
    right:20px;
    top:10px;
    color:white;
}