@import '~antd/dist/antd.css';

.regis{
    width:500px;
    height:340px;
    position: fixed;
    left:40%;
    top:20%;
    border:1px solid #444;
    background: white;
}
.regis h2{
    height:40px;
    background: #444;
    line-height: 40px;
    color:white;
}

button{
    margin-top:15px;
}
.lg2{
    padding:30px 140px;
}
.lg2 input{
    margin-top:6px;
    width:216px;
    
    border:1px solid #999;
}
.lg2 input:nth-child(4){
    width:10px;
    height:10px;
}
.regis i{
    position: absolute;
    right:20px;
    top:10px;
    color:white;
}
.regis .foot{
    border-top:1px solid #999;
    margin-top:12px;
    height:68px;
    background: #eee;
    
    line-height: 68px;
    padding: 0 20px;
}